import mongoose from 'mongoose';
const Schema = mongoose.Schema;

let Learner = new Schema({
    first_name: 
    {
        type: String,
        required: true
    },
    last_name: 
    {
        type: String,
        required: true
    },
    birth_date: 
    {
        type: Date,
        required: true
    },
    contact_number: Number,
    email: {
        type: String,
        required: true
    },
    gender: String,
    address: String,
    img_url: {
        type: String,
        default: 'und'
    },
    interested_trainings: [
        Schema.Types.ObjectId
    ]
}, {
    timestamps: true
});

module.exports = mongoose.model('Learner', Learner);